*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
}
.main_div{
    width: 100vw;
    min-height: 100vh;
    height: 100%;
    background-color: black;
    color: #777;
    display: flex;
    align-items: center;
    justify-content: center;
}
.child_div{
    width: 300px;
    border: 1px solid #7777775b;
    border-radius: 5px;
    padding: 5px;
}
.title_image{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10px 5px;
}
.title_image > img {
    width: 110px;
    margin: 10px 5px;
}
.title_image > span {
    font-size: 1.2rem;
    font-weight: bold;
    text-transform: uppercase;
}
.addItems{
    width: 100%;
    height: 30px;
    display: flex;
    align-items: center;
    background-color: #b3b3b31f;
    border: 1px solid #77777767;
    border-radius: 3px;
    margin: 10px 0px;
}
.addItems > input {
    width: 85%;
    height: 100%;
    padding: 0px 5px;
    background-color: #77777700;
    border: none;
    color: #fff;
    font-size: 1rem;
}
.addItems > span {
    width: 15%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.2s all;
}
.addItems > span:hover{
    background-color: #77777738;
    color: #fff;
    cursor: pointer;
}
.show_items{
    width: 100%;
    height: auto;
    margin: 10px 0px;
}
.items{
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    background-color: #77777765;
    border-radius: 3px;
    color: #ddd;
    padding: 0px 5px;
    transition: 0.2s all;
    margin: 2px 0px;
}
.items:hover {
    background-color: #7777777a;
    border-radius: 3px;
    color: #ffffff98;
}
.items > h3 {
    width: 70%;
    height: 100%;
}
.items > span {
    width: 15%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.2s all;
    display: none;
}
.items > span:hover {
    cursor: pointer;
    display: block;
}
.items:hover span {
    color: #777;
    cursor: pointer;
    display: block;
}

.remove_btn{
    width: 100%;
    height: 40px;
    padding: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn{
    width: 150px;
    height: 100%;
    border-radius: 3px;
    position: relative;
    background-color: #77777752;
}
.btn > button{
    width: 100%;
    height: 100%;
    border: none;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 3px;
    background-color: rgba(255, 222, 173, 0);
    color: #fff;
}
.btn > span {
    position: absolute; 
    transition: 0.2s all;
    background-color: #fff;
}
.span1{
    left: 0;
    top: 0;
    width: 1px;
    height: 0%;
}
.span2{
    right: 0;
    bottom: 0;
    width: 1px;
    height: 0%;
}
.span3{
    top: 0;
    left: 0;
    width: 0%;
    height: 1px;
}
.span4{
    bottom: 0;
    right: 0;
    width: 0%;
    height: 1px;
}
.btn:hover .span1{
    width: 1px;
    height: 100%;
}
.btn:hover .span2{
    width: 1px;
    height: 100%;
}
.btn:hover .span3{
    width: 100%;
    height: 1px;
}
.btn:hover .span4{
    width: 100%;
    height: 1px;
}



/* media quiry for responsive */

@media only screen and (max-width: 330px) {
    .child_div{
        width: 80%;
    }
    .title_image > span {
        font-size: 14px;
    }
}
@media only screen and (max-width: 240px){
    .title_image > span {
        font-size: 10px;
    }
    .addItems > input{
        font-size: 10px;
    }
    .items > h3 {
        font-size: 16px;
    }

    .btn{
        width: auto;
    }
    .btn > button{
        font-size: 14px;
        padding: 0px 10px;
    }

}